import { Phone_Svg } from "./Phone";
import Phone_sphere_1 from "../../assets/images/phone_sphere_1.svg";
import Phone_sphere_2 from "../../assets/images/phone_sphere_2.svg";
import Phone_sphere_3 from "../../assets/images/phone_sphere_3.svg";
import man_1 from "../../assets/images/man_1.svg";
import man_2 from "../../assets/images/man_2.svg";
import man_3 from "../../assets/images/man_3.svg";
import styled from "styled-components";
import { css } from "styled-components";

const IntroPhone = styled.div`
  width: 100%;
  max-width: 600px;
  margin: auto;
  margin-top: 20px;
  height: 635px;
  position: relative;

  & img {
    position: absolute;
  }
`;

const block = css`
  opacity: 0;
  animation: block_move 2s;
  animation-fill-mode: forwards;
`;

const PhoneSvg = styled.div`
  display: flex;
  width: 70%;
  margin: auto;

  & svg {
    width: 100%;
    margin: auto;
  }

  & .block_3 {
    ${block}
    animation-delay: 4.5s;
  }
  & .block_2 {
    ${block}
    animation-delay: 3s;
  }
  & .block_1 {
    ${block}
    animation-delay: 1.5s;
  }
  & .nav {
    ${block}
    animation-delay: 0s;
  }

  @keyframes block_move {
    0% {
      transform: translateY(25px);
      opacity: 0;
    }
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }
`;

const PhoneSphere_1 = styled.img`
  z-index: 0;
  top: 90%;
  left: 20%;

  animation: man_move_1 10s ease-in-out 3s infinite;

  @keyframes sphere_move_1 {
    50% {
      transform: translateY(20px) rotate(-3deg);
    }
  }
`;

const PhoneSphere_2 = styled.img`
  z-index: 0;
  top: 2%;
  left: 78%;

  animation: man_move_2 15s ease-in-out 0s infinite;

  @keyframes sphere_move_2 {
    50% {
      transform: translateY(20px) rotate(-3deg);
    }
  }
`;

const PhoneSphere_3 = styled.img`
  z-index: 0;
  top: 55%;
  left: 78%;

  animation: man_move_1 25s ease-in-out 15s infinite;

  @keyframes sphere_move_3 {
    50% {
      transform: translateY(20px) rotate(-3deg);
    }
  }
`;

const Man1 = styled.img`
  width: 15%;
  max-width: 70px;
  top: 10%;
  left: 5%;

  animation: man_move_1 15s ease-in-out 0s infinite;

  @keyframes man_move_1 {
    50% {
      transform: translateY(20px) rotate(-3deg);
    }
  }
`;

const Man2 = styled.img`
  width: 15%;
  max-width: 84px;
  top: 55%;
  left: 0%;

  animation: man_move_2 25s ease-in-out 5s infinite;

  @keyframes man_move_2 {
    50% {
      transform: translatex(35px) translateX(-15px) rotate(-3deg);
    }
  }
`;

const Man3 = styled.img`
  width: 15%;
  max-width: 69px;

  top: 34%;
  left: 85%;

  animation: man_move_3 20s ease-in-out 15s infinite;
  animation-delay: 0.4s;

  @keyframes man_move_3 {
    50% {
      transform: translateY(-20px) translateX(-10px) rotate(-3deg);
    }
  }
`;

const PhoneMobile = () => {
  return (
    <IntroPhone>
      <PhoneSvg>{Phone_Svg}</PhoneSvg>
      <PhoneSphere_1 loading="lazy" src={Phone_sphere_1} />
      <PhoneSphere_2 loading="lazy" src={Phone_sphere_2} />
      <PhoneSphere_3 loading="lazy" src={Phone_sphere_3} />
      <Man1 loading="lazy" src={man_1} />
      <Man2 loading="lazy" src={man_2} />
      <Man3 loading="lazy" src={man_3} />
    </IntroPhone>
  );
};

export default PhoneMobile;
